class App extends Component {

    constructor() {
        super();
        this.state = {
            title: "My MSA",
            alignment: sampleAln,
            included: Object.keys(sampleAln),
            excluded: [],
            series: sampleSeries,
            zoom: 10,
            color_scheme: "Flower",
            show_letters: true,
            allow_sequence_selection: true,
        };
        this.setProps = this.setProps.bind(this);
    }

    setProps(newProps) {
        this.setState(newProps);
    }

    render() {
        return (
            <div>
                <DashSeqaln
                    setProps={this.setProps}
                    {...this.state}
                />
            </div>
        )
    }
}

export default App;
